export const errorIdentifier = (error) => {
	const status = error.response?.status
	switch (status) {
		case 400:
			console.log(`*---  ${status}  ---*\n*---     ---*`)
			break
		case 401:
			console.log(`*---  ${status} ---*\n*---     ---*`)
			break
		case 500:
			console.log(`*---  ${status} ---*\n*---     ---*`)
			return true
		default:
			console.log(`*---  ${status} ---*`)
	}
}